<template>
  <div class="catalogue">
    <h1>Catalogue</h1>
    <p>Welcome to the catalogue page of MyWay.</p>
    <input v-model="searchQuery" @input="searchCars" placeholder="Search for cars..." />
  </div>
  <div>
    <h1>Cars</h1>
    <ul>
      <li v-for="car in cars" :key="car.id" style="display: flex;justify-content: center;align-items: center;">
        <img :src="car.image" alt="Car Image" width="100px" height="50px"/>
        {{ car.title }} - {{ car.brand }} - {{ car.model }} - ${{ car.price }} -
        <router-link :to="{ name: 'CarDetail', params: { id: car.id } }">View more</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'CataloguePage',
  data() {
    return {
      cars: [],
      searchQuery: ''
    };
  },
  created() {
    this.fetchCars();
  },
  methods: {
    fetchCars() {
      axios.get('/api/cars/')
        .then(response => {
          this.cars = response.data;
        })
        .catch(error => {
          console.error('There was an error fetching the cars!', error);
        });
    },
    searchCars() {
      if (this.searchQuery.trim() === '') {
        this.fetchCars();
        return;
      }
      axios.get(`/api/cars/search?q=${this.searchQuery}`)
        .then(response => {
          this.cars = response.data;
        })
        .catch(error => {
          console.error('There was an error searching for cars!', error);
        });
    }
  }
}
</script>

<style scoped>
.catalogue {
  text-align: center;
  margin-top: 50px;
}
</style>